import { Component, Input, Output, EventEmitter, HostBinding } from '@angular/core';

@Component({
    selector: 'info-pane-component',
    template: require('./info-pane.component.html'),
    styles:[require('./info-pane.component.scss')]
})
export class InfoPaneComponent {

    @Input() goals: Object;
    @Input() owner: Object;
    @Output() nextPhaseEvent: EventEmitter<boolean> = new EventEmitter();
    @Output() settingsEvent: EventEmitter<boolean> = new EventEmitter();
    @HostBinding('class') allClasses = 'card four wide column';

    onSettings(){
        this.settingsEvent.emit(true);
    }

    onNextPhase(){
        this.nextPhaseEvent.emit(true);
    }

    checkUser(){
        if( this.owner['uid'] == JSON.parse( localStorage.getItem('currentUser') ).uid ){
            return true;
        }else{
            return false;
        }
    }

}


